/**
 *
 * taken from:
 * https://github.com/DylanPiercey/q-flat
 *
 * @description
 * Creates a querystring style object from a nested one.
 *
 * @example
 * var result = flatten({ a: { b: 1 }, c: { d: 1 } })
 * result //-> { "a[b]": 1, "c[d]": 2 }
 *
 * @param obj The object to flatten.
 */

export function jsonToQueryParamsObject(json): any {
  const { toString, hasOwnProperty } = Object.prototype
  const ObjectType = "[object Object]"
  const ArrayType = "[object Array]"

  /**
   * Join path keys using query string `a[b]` style syntax.
   */
  let join = (path: string | void, key: string) => {
    return path != null ? path + "[" + key + "]" : key
  }

  let flatten = (obj: any, path?: string, result?: any) => {
    const type = toString.call(obj)

    if (result === undefined) {
      if (type === ObjectType) {
        result = {}
      } else if (type === ArrayType) {
        result = []
      } else {
        return
      }
    }

    if (type === ArrayType) {
      //result[join(path, '[]')] = obj
      console.log("The path is " + path)
      result[path] = obj
    }
    else {
      for (const key in obj) {
        /* istanbul ignore if */
        if (!hasOwnProperty.call(obj, key)) {
          continue
        }

        const val = obj[key]
        /*
        if (val == null) {
          continue
        }
        */

        switch (toString.call(val)) {
          case ArrayType:
            let resultKey = join(path, key) + "[]"
            result[resultKey] = val
            break
          case ObjectType:
            flatten(val, join(path, key), result)
            break
          default:
            result[join(path, key)] = val
            break
        }
      }
    }


    return result
  }
  
  return flatten(json)
}
