/**
 * convert a string into its snake-cased (aka "underscored") counterpart
 * @param value
 */

import * as _ from "lodash"

// _.snakeCase('Foo Bar')
// => 'foo_bar'
 
_.snakeCase('fooBar')
// => 'foo_bar'
 
_.snakeCase('--FOO-BAR--')
// => 'foo_bar'

export function snakeCase(value: string) {
  return _.snakeCase(value)
}
